import Vue from 'vue'
import VueRouter from 'vue-router'

import lancar from '@/components/contrato/lancar'
import manutencao from '@/components/manutencao/alterar'
import relatorio from '@/components/relatorio/relatorio'
import home from '@/components/home/home'

Vue.use(VueRouter)

const routes =[{
    
    name:'home',
    path:'/',
    component:home

},{
    name:'lancar',
    path:'/new',
    component:lancar

},{
    name:'manutencao',
    path:'/manutencao',
    component:manutencao

},{
    name:'relatorio',
    path:'/relatorio',
    component:relatorio

}
]

export default new VueRouter({
    mode:'history',
    routes
})